body {
  padding: 30px;
}

.form {
  border: 1px solid #ccc;
  padding: 20px;
}

.form-label {
  display: block;
  margin: 25px 10px;
  width: 320px;
  position: relative;
}

.form-label input {
  width: 150px;
  float: right;
}

.form-button {
  -webkit-appearance: none;
  width: 80px;
  margin-left: 50px;
  height: 26px;
  background-color: #fff;
  color: #000;
  border: 1px solid #ccc;
  font-size: 16px;
  outline: none;
  cursor: pointer;
}

.label-error {
  color: red;
  height: 20px;
  line-height: 20px;
  font-size: 14px;
  margin: 0;
  position: absolute;
  width: 800px;
  bottom: -20px;
}



